<template>
  <form class="inputBox shadow" ref="form" @submit="stopSubmit">
    <input type="text" name="text" v-model="todoInput" v-on:keyup.enter="submitData">
    <button class="addContainer" type="button" v-on:click="submitData">
      <i class="fas fa-plus addBtn"></i>
    </button>
  </form>
</template>

<script>
export default {
  data() {
    return {
      todoInput: "",
    }
  },
  methods: {
    stopSubmit(event) {
      event.preventDefault();
    },
    submitData() {
      let formData = new FormData();
      formData.append('text', this.todoInput);

      let contact = {};
      formData.forEach(function(value, key){
        contact[key] = value;
      });

      if (!contact['text']) {
        return false;
      }

      this.axios({
        method: 'post',
        url: 'https://joohoon.kr/php/todo/input.php',
        data: formData,
        config: { headers: {'Content-Type': 'multipart/form-data, image/png'}}
      })
      .then(response => {
        this.todoInput = "";
        this.$refs.form.reset();
      })
      .catch(error => {
        // error;
      });
    },
  },
}
</script>

<style scoped>
input:focus {
  outline: none;
}
.inputBox {
  background-color: white;
  height: 50px;
  line-height: 50px;
  border-radius: 5px;
  font-size: 0
}
.inputBox:after {
  content: "";
  display: block;
  clear: both;
}
.inputBox input {
  float: left;
  width: calc(100% - 50px);
  height: 100%;
  border-style: none;
  font-size: 0.9rem;
  box-sizing: border-box;
}
.addContainer {
  float: right;
  background: linear-gradient(to right, #6478FB, #8763FB);
  display: block;
  width: 50px;
  height: 100%;
  border-radius: 0 5px 5px 0;
  padding: 0;
  cursor: pointer;
}
.addBtn {
  color: white;
  vertical-align: middle;
  font-size: 14px;
}
</style>


