<template>
  <ul>
    <li class="shadow" v-for="(todoItem, index) in todoItems" v-bind:key="todoItem['id']">
      <p class="todoList">{{todoItem['text']}}</p>
      <button class="removeContainer" type="button" v-on:click="removeTodo(todoItem, index)">
        <i class="removeBtn fas fa-trash-alt"></i>
      </button>
    </li>
  </ul>
</template>

<script>
export default {
  data() {
    return {
      todoItems: [],
    }
  },
  created() {
    this.getTable();
  },
  methods: {
    removeTodo(todoItem, index) {

      let formData = new FormData();
      formData.append('delete', this.todoItems[index]['id']);

      this.axios({
        method: 'post',
        url: 'https://joohoon.kr/php/todo/delete.php',
        data: formData,
        config: { headers: {'Content-Type': 'multipart/form-data'}}
      })
      .then(response => {
        this.todoItems.splice(index,1);
      })
      .catch(error => {
        // error;
      });
    },
    getTable() {
      this.axios.get('https://joohoon.kr/php/todo/output.php')
      .then(response => {
        let getData = (new Function("return [" + response.data + "];")());
        for (let i = 0; i < getData.length; i++) {
          this.todoItems.push({
            "id": getData[i]['id'],
            "text": getData[i]['text'],
            "complete": getData[i]['complete'],
          });
        }
      });
    },
  },
}
</script>

<style scoped>
ul {
  text-align: left;
}
li {
  display: block;
  margin: 0.5rem 0;
  height: 50px;
  min-height: 50px;
  background: white;
  border-radius: 5px;
}
li:after {
  content: "";
  display: block;
  clear: both;
}
.todoList {
  float: left;
  display: block;
  padding: 0 0.9rem;
  height: 50px;
  min-height: 50px;
  width: calc(100% - 50px);
  line-height: 50px;
  box-sizing: border-box;
}
.removeContainer {
  float: right;
  display: block;
  width: 50px;
  height: 100%;
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
}
.removeBtn {
  color: #de4343;
  vertical-align: middle;
  font-size: 14px;
}
.checkBtn {
  line-height: 45px;
  color: #62acde;
  margin-right: 5px;
}
.checkBtnCompleted {
  color: #b3adad;
}
</style>


