<template>
  <div>
    <router-view/>
    work<br>
    {{phpTable}}
    <router-link class="gnb_item" to="/work/workDetail">goto workDetail</router-link>
  </div>
</template>

<script>
export default {
  name: 'work',
  data() {
    return {
      phpTable : 'phpTable',
    }
  },
  created() {
    this.axios.get('https://joohoon.kr/php/mysql/output.php')
    .then(response => {
      this.getTable(response.data);
    });
  },
  methods : {
    getTable(getData) {
      var getData_split = getData.split('<br>');
      for (let i = 0; i < getData_split.length - 1; i++) {
        var thisData = JSON.parse(getData_split[i]);
        console.log(thisData['id']);
        console.log(thisData['title']);
        console.log("https://joohoon.kr/php/mysql/" + thisData['image_target']);
      }
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



