import Vue from 'vue'
import Router from 'vue-router'

import home from '@/components/page/home';
import todo from '@/components/page/todo';
import about from '@/components/page/about';
import work from '@/components/page/work';
import culture from '@/components/page/culture';
import contact from '@/components/page/contact';

import workDetail from '@/components/page/workDetail';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      components: {
        default: home,
      },
    },
    {
      path: '/todo',
      components: {
        default: todo,
      },
    },
    {
      path: '/about',
      components: {
        default: about,
      },
    },
    {
      path: '/work',
      components: {
        default: work,
      },
    },
    {
      path: '/work/workDetail',
      components: {
        default: work,
      },
      children: [
        { path: '', component: workDetail },
      ],
    },
    {
      path: '/culture',
      components: {
        default: culture,
      },
    },
    {
      path: '/contact',
      components: {
        default: contact,
      },
    },
  ]
})



