<template>
  <div>
    <h2>[getTable]</h2>
    <p><button @click="getTable" type="button">getTable</button></p>
    <p>{{phpTable}}</p>
    <br>
    <h2>[getTime]</h2>
    <p><button @click="getTime" type="button">getTime</button></p>
    <p>{{phpTime}}</p>
    <br>
    <br>
    <h2>[input]</h2>
    <form ref="form" @submit="submitData">
      <p><input type="text" name="title" v-model="title" placeholder="title"></p>
      <p><input type='file' name='image' @change="processFile($event)"></p>
      <p><input type="submit" value="요청"></p>
      <p>{{image_err}}</p>
    </form>
    <br>
    <h2>[output]</h2>
    <form action="https://joohoon.kr/php/mysql/output.php" method="POST">
      <p><input type="text" name="area" placeholder="범위 ex)8, ex)3~20"></p>
      <p><input type="submit" value="요청"></p>
    </form>
  </div>
</template>

<script>
export default {
  name: 'home',
  data() {
    return {
      phpTime : 'nowTime',
      phpTable : 'phpTable',
      title: '',
      image: '',
      image_err: '',
    }
  },
  methods : {
    processFile(event) {
      this.image = event.target.files[0];
    },
    getTime() {
      this.axios.get('https://joohoon.kr/php/getTime/getTime.php')
      .then(response => {
        this.phpTime = response.data;
      });
    },
    getTable() {
      this.axios.get('https://joohoon.kr/php/mysql/output.php')
      .then(response => {
        this.phpTable = response.data;
      });
    },
    submitData(e) {
      e.preventDefault();

      let formData = new FormData();
      formData.append('title', this.title);
      formData.append('image', this.image);

      let contact = {};
      formData.forEach(function(value, key){
        contact[key] = value;
      });

      if (!contact['title'] || !contact['image']) {
        this.image_err = '누락된 필수데이터가 존재합니다.';
        return false;
      }

      this.axios({
        method: 'post',
        url: 'https://joohoon.kr/php/mysql/input.php',
        data: formData,
        config: { headers: {'Content-Type': 'multipart/form-data, image/png'}}
      })
      .then(response => {
        this.$refs.form.reset();
      })
      .catch(error => {
        // error;
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


